// import React from 'react';
import ReactDOM from 'react-dom';
import './index.css'

var playeds     = [ 2,2,2,
                    2,2,2,
                    2,2,2]

var winPlayList = [[0,1,2],
                   [3,4,5],
                   [6,7,8],
                  
                   [0,3,6],
                   [1,4,7],
                   [2,5,8],
                   
                   [0,4,8],
                   [6,4,2]]

var player = 0

var App = {
  reload(){
    if(!this.win()){
      ReactDOM.render(
        <Main/>,
        document.getElementById('root')
      );
    }else{
      ReactDOM.render(
        <>
          <End/>
          <Main/>
        </>,
        document.getElementById('root')
      );
    }
  },
  win(){
    var answer = false
    for (var i of winPlayList){
      if(playeds[i[0]] === playeds[i[1]] && playeds[i[1]] === playeds[i[2]]){
        if(playeds[i[0]] !== 2){
          // console.log("Itens:", playeds[i[0]], playeds[i[1]], playeds[i[2]])'
          answer = true
        }
      }
    }
    let n = 0
    for (i of playeds){
      if (i !== 2){
        n++
      }
    }
    if(n === playeds.length){
      answer = true
    }

    return answer
  },
  restart(){
    player  = 1
    playeds = [2,2,2,2,2,2,2,2,2]
    App.reload()
  },
  play(n){
    if(playeds[n] === 2){
      playeds[n] = 1
      player     = 0
      App.bot()
      App.reload()
      console.log(player)
      console.log(playeds)
    }
  },
  bot(){
    function getRandomArbitrary(min, max) {
      return Math.random() * (max - min) + min;
    }

    let list = []
    for(var index in playeds){
      if(playeds[index] === 2){
        list.push(index)
      }
    }
    console.log(list, list.length)
    let random_index_to_list = getRandomArbitrary(0, list.length-1).toFixed()
    playeds[list[random_index_to_list]] = 0
    player = 1
  }
}

function End(){
  return(
    <div style={{textAlign:"center", background:"rgb(34, 9, 73)"}}>
      <h1>End game bro</h1>
      <button onClick={App.restart}> Restart </button>
    </div>
  )
}

function Iten(props){
  if(props.h === 0){
    return(
      <div>
        <svg viewBox="0 0 100 100" width="100" height="100">
          <circle cx="50" cy="50" r="40" fill="transparent" stroke="white" strokeWidth="6px"/>
        </svg>
      </div>)
  }else{
    if(props.h === 1){
      return(
        <div>
          <svg viewBox="0 0 100 100" width="100" height="100">
            <line x1="20" y1="20" x2="80" y2="80" stroke="white" fill="white" strokeWidth="6px" />
            <line x1="80" y1="20" x2="20" y2="80" stroke="white" fill="white" strokeWidth="6px" />
          </svg>
        </div>)
    }else{
      if(props.h === 2){
        return(
          <div>
            <svg viewBox="0 0 100 100" width="100" height="100">
            <circle cx="50" cy="50" r="40" fill="transparent" stroke="transparent" strokeWidth="6px"/>
            </svg>
          </div>)
      }
    }
  }
  
}

function Body(props){
  return(
      <table>
        <tbody>
          <tr>
            <td onClick={()=>{App.play(0)}}>
              <Iten h={props.h1}/>
            </td>
            <td onClick={()=>{App.play(1)}}>
              <Iten h={props.h2}/>
            </td>
            <td onClick={()=>{App.play(2)}}>
              <Iten h={props.h3}/>
            </td>
          </tr>
          <tr>
            <td onClick={()=>{App.play(3)}}>
              <Iten h={props.h4}/>
            </td>
            <td onClick={()=>{App.play(4)}}>
              <Iten h={props.h5}/>
            </td>
            <td onClick={()=>{App.play(5)}}>
              <Iten h={props.h6}/>
            </td>
          </tr>
      
          <tr>
            <td onClick={()=>{App.play(6)}}>
              <Iten h={props.h7}/>
            </td>
            <td onClick={()=>{App.play(7)}}>
              <Iten h={props.h8}/>
            </td>
            <td onClick={()=>{App.play(8)}}>
              <Iten h={props.h9}/>
            </td>
          </tr>
        </tbody>
      </table>
)}

function Main(){
  return(
    <div id="divMain">
      <div>
        <h1>
          Tic Tac Toe
        </h1>
        <Body h1={playeds[0]} h2={playeds[1]} h3={playeds[2]}
              h4={playeds[3]} h5={playeds[4]} h6={playeds[5]}
              h7={playeds[6]} h8={playeds[7]} h9={playeds[8]}/>
        <p>
          Gabriel Botelho Malenowitch
        </p>
      </div>
    </div>
)}


ReactDOM.render(
  <Main/>,
  document.getElementById('root')
);

